import { Button, Card, Form, FormInstance, message, Modal, Select, Table } from 'antd'
import React from 'react' //  React V15.5  react 
import { cityService } from './service';
import { common } from '../../common/public';

export default class City extends React.Component {


    state = {
        dataSource: [],
        curPage: 1,
        pageSize: 10,
        total: 30,
        pagination: null,
        isShowOpenCity: false,
    }

    ModalFormRef: OpenCityForm


    componentDidMount() {
        this.requestList();
    }
    /**
     * 
     */
    requestList = () => {
        const params = {
            page: this.state.curPage
        }
        cityService.getTableList(params).then(res => {
            if (res.status === 0) {
                this.setState({
                    dataSource: res.list.map((item, index) => {
                        item.key = index
                        return item;
                    }),
                    curPage: res.page,
                    pageSize: res.pageSize,
                    total: res.total,
                    pagination: common.pagination(res, (current) => {
                        /* TODO (mock) */
                        this.state.curPage = current;
                        this.requestList();
                    })
                })
            } else {
                message.info(res.msg);
            }
        })
    }

    /**
     * 
     */
    handleOpenCity = () => {
        this.setState({ isShowOpenCity: true })
    }

    /**
     * 
     */
    handleSubmit = () => {
        console.log(this.ModalFormRef.formRef.current.getFieldsValue());
        const params = this.ModalFormRef.formRef.current.getFieldsValue()
        cityService.getTableList(params).then(res => {
            if (res.status === 0) {
                message.success('');
                this.setState({ isShowOpenCity: false })
                this.requestList();
            } else {
                message.warning(res.msg)
            }
        })


    }

    // DOM render  DOM
    render() {
        const columns = [
            {
                title: 'ID',
                dataIndex: 'id'
            },
            {
                title: '',
                dataIndex: 'name'
            },
            {
                title: '',
                dataIndex: 'mode',
                render(mode) {
                    return mode == 1 ? '' : '';
                }
            },
            {
                title: '',
                dataIndex: 'op_mode',
                render(op_mode) {
                    return op_mode == 1 ? '' : '';
                }
            },
            {
                title: '',
                dataIndex: 'franchisee_name'
            },
            {
                title: '',
                dataIndex: 'city_admins',
                render(arr) {
                    return arr.map((item) => {
                        return item.user_name;
                    }).join(',');
                }
            },
            {
                title: '',
                dataIndex: 'open_time'
            },
            {
                title: '',
                dataIndex: 'update_time',

            },
            {
                title: '',
                dataIndex: 'sys_user_name'
            }
        ]
        const { dataSource, pagination } = this.state
        return (<div>
            <Card>
                <FilterForm />
            </Card>
            <Card style={{ marginTop: 10 }}>
                <Button type="primary" onClick={this.handleOpenCity}></Button>
            </Card>
            <div className="content-wrap">
                <Table columns={columns} dataSource={dataSource} pagination={pagination} />
            </div>
            <Modal
                title=""
                visible={this.state.isShowOpenCity}
                onCancel={() => {
                    this.setState({
                        isShowOpenCity: false
                    })
                }}
                onOk={this.handleSubmit}
            >
                <OpenCityForm ref={(node) => { this.ModalFormRef = node; }} />
            </Modal>
        </div>)
    }
}
