import { Button, Card, DatePicker, Form, FormInstance, message, Modal, Progress, Select, Table } from 'antd'
import moment from 'moment'
import React from 'react' //  React V15.5  react 
import { common } from '../../common/public'
import BaseForm from '../../components/BaseForm'
import LTable from '../../components/LTable'
import { Config } from '../../config/public'
import { orderService } from './service'

const FormItem = Form.Item
export default class Order extends React.Component {

    state = {
        dataSource: [],
        curPage: 1,
        pageSize: 10,
        total: 30,
        pagination: null,
        // 
        selectedRowKeys: [],
        selectedItem: null,
        selectedIds: null,
        orderConfirmVisble: false,
        orderInfo: null,
    }

    formList = [
        {
            type: 'SELECT',
            label: '',
            field: 'city',
            placeholder: '',
            initialValue: '1',
            width: 100,
            list: [{ id: '0', name: '' }, { id: '1', name: '' }, { id: '2', name: '' }, { id: '3', name: '' }]
        },
        {
            type: '',
            field: 'range_picker',
            placeholder: ['', ''],
        },
        {
            type: 'SELECT',
            label: '',
            field: 'order_status',
            placeholder: '',
            initialValue: '1',
            width: 100,
            list: [{ id: '0', name: '' }, { id: '1', name: '' }, { id: '2', name: '' }]
        }
    ]

    componentDidMount() {
        this.request();
    }

    request = () => {
        const params = {
            page: this.state.curPage
        }
        // 
        // requestAjax.requestList(this, '/order/list', params, true);

        // 
        orderService.getTableList(params).then(res => {
            if (res.status === 0) {
                this.setState({
                    dataSource: res.list.map((item, index) => {
                        item.key = index
                        return item;
                    }),
                    curPage: res.page,
                    pageSize: res.pageSize,
                    total: res.total,
                    pagination: common.pagination(res, (current) => {
                        /* TODO (mock) */
                        this.state.curPage = current;
                        this.request();
                    })
                })
            } else {
                message.info(res.msg);
            }
        })
    }

    /**
     * 
     */
    openOrderDetail = () => {
        let item = this.state.selectedItem;
        if (!item) {
            Modal.info({
                title: '',
                content: ''
            })
            return;
        }
        window.open(`./#/common/order/detail/${item.id}`, '_blank')
    }

    /**
     * 
     */
    handleConfirm = () => {
        let item = this.state.selectedItem;
        if (!item) {
            Modal.info({
                title: '',
                content: ''
            })
            return;
        }
        orderService.getDetail().then((res) => {
            if (res.status === 0) {
                this.setState({
                    orderInfo: res.data,
                    orderConfirmVisble: true
                })
            }
        })
    }

    /**
     * 
     */
    handleFinishOrder = () => {
        let item = this.state.selectedItem;
        const params = {
            orderId: item.id
        }
        orderService.endOrder(params).then(res => {
            if (res.status == 0) {
                message.success(res.msg)
                this.setState({
                    orderConfirmVisble: false
                })
                this.request();
            }
        })
    }

    onRowClick = (record, item) => {
        let selectKey = [item];
        this.setState({
            selectedRowKeys: selectKey,
            selectedItem: record
        })
    }

    handleFilter = (params) => {
        // console.log(params);
        if (!params['range_picker']) {
            message.warn('')
            return;
        }
        const start = moment(params['range_picker'][0]).format('YYYY-MM-DD HH:mm:ss');
        const end = moment(params['range_picker'][1]).format('YYYY-MM-DD HH:mm:ss')
        console.log(params, start, end);
        this.request();
    }

    render() {
        const columns = [
            {
                title: '',
                dataIndex: 'order_sn'
            },
            {
                title: '',
                dataIndex: 'bike_sn'
            },
            {
                title: '',
                dataIndex: 'user_name'
            },
            {
                title: '',
                dataIndex: 'mobile'
            },
            {
                title: '',
                dataIndex: 'distance',
                render(distance) {
                    return `${(distance / 1000).toFixed(2)}Km`;
                }
            },
            {
                title: '',
                dataIndex: 'total_time',
                render: (time) => {
                    return `${time}`
                }
            },
            {
                title: '',
                dataIndex: 'status',
                render: (status) => {
                    let tags = {
                        '1': '',
                        '2': '',
                    }
                    return tags[status];
                }
            },
            {
                title: '',
                dataIndex: 'start_time'
            },
            {
                title: '',
                dataIndex: 'end_time'
            },
            {
                title: '',
                dataIndex: 'total_fee'
            },
            {
                title: '',
                dataIndex: 'user_pay'
            }
        ]
        const { dataSource, pagination, orderInfo, selectedRowKeys, selectedItem } = this.state;
        // 
        const rowSelection: any = {
            type: 'radio',
            selectedRowKeys: selectedRowKeys,
        };
        return (
            <div>
                <Card>
                    <BaseForm formList={this.formList} filterSubmit={this.handleFilter} />
                </Card>
                <Card style={{ marginTop: 10 }}>
                    <Button type="primary" onClick={this.openOrderDetail}></Button>
                    <Button type="primary" style={{ marginLeft: 10 }} onClick={this.handleConfirm}></Button>
                </Card>
                <div className="content-wrap">
                    <LTable
                        updateSelectedItem={common.updateSelectedItem.bind(this)}
                        columns={columns}
                        dataSource={dataSource}
                        rowSelection={rowSelection}
                        selectedRowKeys={selectedRowKeys}
                        selectedItem={selectedItem}
                        pagination={pagination}
                    />
                    {/* <Table
                        bordered columns={columns} dataSource={dataSource} pagination={pagination} rowSelection={rowSelection} onRow={(record, item) => {
                            return {
                                onClick: () => {
                                    this.onRowClick(record, item);
                                }
                            };
                        }}
                    /> */}
                </div>
                <Modal
                    title=""
                    visible={this.state.orderConfirmVisble}
                    onCancel={() => {
                        this.setState({
                            orderConfirmVisble: false
                        })
                    }}
                    onOk={this.handleFinishOrder}
                    width={600}
                >
                    <Form layout="horizontal">
                        <FormItem label="" {...Config.getFormLayout}>
                            {orderInfo?.bike_sn || '-'}
                        </FormItem>
                        <FormItem label="" {...Config.getFormLayout}>
                            <Progress
                                strokeColor={{
                                    '0%': '#108ee9',
                                    '100%': '#87d068',
                                }}
                                percent={orderInfo?.battery | 0}
                            />
                        </FormItem>
                        <FormItem label="" {...Config.getFormLayout}>
                            {orderInfo?.start_time || '-'}
                        </FormItem>
                        <FormItem label="" {...Config.getFormLayout}>
                            {orderInfo?.location || '-'}
                        </FormItem>
                    </Form>
                </Modal>
            </div>
        )
    }
}