import { Button, Card, DatePicker, Form, FormInstance, Input, message, Modal, Radio, Select, Spin } from 'antd'
import { DeleteOutlined, EditOutlined, InfoCircleOutlined, PlusOutlined } from '@ant-design/icons';
import React from 'react'
import { common } from '../../common/public';
import BaseForm from '../../components/BaseForm';
import LTable from '../../components/LTable';
import { userService } from './service';
import moment from 'moment';

const FormItem = Form.Item;
const RadioGroup = Radio.Group;
const Option = Select.Option;
const TextArea = Input.TextArea
export default class User extends React.Component {

    state = {
        dataSource: [],
        curPage: 1,
        pageSize: 10,
        total: 30,
        pagination: null,
        // 
        selectedRowKeys: [],
        selectedItem: null,
        type: null,
        isVisible: false,
        title: null,
        userInfo: null,
    }

    userFormRef: UserForm

    formList = [
        {
            type: 'INPUT',
            label: '',
            field: 'user_name',
            placeholder: '',
            width: 130,
        }, {
            type: 'INPUT',
            label: '',
            field: 'user_mobile',
            placeholder: '',
            width: 140,
        }, {
            type: 'DATE',
            label: '',
            field: 'user_date',
            placeholder: '',
        }
    ]

    handleFilter = (params) => {
        console.log(params);
        if (!params['user_date']) {
            message.warn('')
            return;
        }
        this.request();
    }

    componentDidMount() {
        this.request();
    }

    request = () => {
        const params = {
            page: this.state.curPage
        }
        userService.getTableList(params).then(res => {
            if (res.status === 0) {
                this.setState({
                    dataSource: res.list.map((item, index) => {
                        item.key = index
                        return item;
                    }),
                    curPage: res.page,
                    pageSize: res.pageSize,
                    total: res.total,
                    pagination: common.pagination(res, (current) => {
                        /* TODO (mock) */
                        this.state.curPage = current;
                        this.request();
                    }),
                    selectedRowKeys: [],
                    selectedItem: null,
                })
            } else {
                message.info(res.msg);
            }
        })
    }

    add = (type: string) => {
        this.setState({
            type,
            isVisible: true,
            title: '',
            selectedRowKeys: [],
            selectedItem: null,
        });
    }

    edit = (type: string, item) => {
        if (!item) {
            Modal.warn({
                title: "",
                content: ''
            });
            return;
        }
        this.setState({
            type,
            isVisible: true,
            title: '',
            userInfo: item,
        });
        /* ,form */
        setTimeout(() => {
            this.userFormRef.form.current.resetFields();
        }, 100);
    }

    detail = (type: string, item) => {
        if (!item) {
            Modal.warn({
                title: "",
                content: ''
            });
            return;
        }
        this.setState({
            type,
            isVisible: true,
            title: '',
            userInfo: item,
        });
    }

    del = (type: string, item) => {
        if (!item) {
            Modal.warn({
                title: "",
                content: ''
            });
            return;
        }
        let _this = this;
        Modal.confirm({
            title: '',
            content: '',
            onOk() {
                const params = {
                    id: item.id
                }
                userService.oprateUser(type, params).then((res) => {
                    if (res.status === 0) {
                        message.success(res.msg);
                        _this.request();
                    } else {
                        message.warn(res.msg);
                    }
                })
            }
        });
    }

    /**
     * 
     * @param type 
     */
    hanleOperate = (type) => {
        let item = this.state.selectedItem;
        const targetMaps = {
            add: () => { this.add(type) },
            edit: () => { this.edit(type, item) },
            detail: () => { this.detail(type, item) },
            del: () => { this.del(type, item) },
        }
        let action = targetMaps[type];
        action();
    };

    /**
     * /
     */
    handleSubmit = () => {
        let type = this.state.type;
        /* 
        
         Form.Provider Modal  Form 
         form.submit  <Button htmlType="submit" />  web  
        */
        // this.userFormRef.form.current.submit();
        this.userFormRef.form.current.validateFields().then(values => {
            userService.oprateUser(type, values).then(res => {
                if (res.status === 0) {
                    message.success(res.msg);
                    this.userFormRef.form.current.resetFields();
                    this.setState({
                        isVisible: false
                    })
                    this.request();
                } else {
                    message.warn(res.msg);
                }
            })
        }).catch(info => {
            console.log('', info);
        });
    }

    handleCancel = () => {
        this.userFormRef.form.current.resetFields();
        this.setState({
            isVisible: false,
        })
    }

    render() {
        const columns = [
            {
                title: 'id',
                dataIndex: 'id'
            }, {
                title: '',
                dataIndex: 'username'
            }, {
                title: '',
                dataIndex: 'sex',
                render(sex) {
                    return sex == 1 ? '' : '';
                }
            }, {
                title: '',
                dataIndex: 'state',
                render(state) {
                    return {
                        '1': '',
                        '2': '',
                        '3': '',
                        '4': 'FE',
                        '5': ''
                    }[state];
                }
            }, {
                title: '',
                dataIndex: 'interest',
                render(interest) {
                    return {
                        '1': '',
                        '2': '',
                        '3': '',
                        '4': '',
                        '5': '',
                        '6': '',
                        '7': '',
                        '8': ''
                    }[interest];
                }
            }, {
                title: '',
                dataIndex: 'birthday'
            }, {
                title: '',
                dataIndex: 'address'
            }, {
                title: '',
                dataIndex: 'time'
            },
        ];
        /*  */
        let footer = {};
        if (this.state.type == 'detail') {
            footer = {
                footer: null
            };
        }
        const { dataSource, pagination, selectedRowKeys, selectedItem, title, isVisible, type, userInfo } = this.state;
        // 
        const rowSelection: any = {
            type: 'radio',
            selectedRowKeys: selectedRowKeys,
        };
        return (<div>
            <Card>
                <BaseForm formList={this.formList} filterSubmit={this.handleFilter} />
            </Card>
            <Card style={{ marginTop: 10 }} className="operate-wrap">
                <Button type="primary" icon={<PlusOutlined />} onClick={() => this.hanleOperate('add')}></Button>
                <Button type="primary" icon={<EditOutlined />} onClick={() => this.hanleOperate('edit')}></Button>
                <Button type="primary" icon={<InfoCircleOutlined />} onClick={() => this.hanleOperate('detail')}></Button>
                <Button type="primary" danger icon={<DeleteOutlined />} onClick={() => this.hanleOperate('del')}></Button>
            </Card>
            <div className="content-wrap">
                <LTable
                    updateSelectedItem={common.updateSelectedItem.bind(this)}
                    columns={columns}
                    dataSource={dataSource}
                    rowSelection={rowSelection}
                    selectedRowKeys={selectedRowKeys}
                    selectedItem={selectedItem}
                    pagination={pagination}
                />
            </div>
            <Modal
                title={title}
                visible={isVisible}
                onOk={this.handleSubmit}
                onCancel={this.handleCancel}
                width={600}
                {...footer}
            >
                <UserForm type={type} userInfo={userInfo} ref={(inst) => { this.userFormRef = inst; }} />
            </Modal>
        </div>)
    }
}

interface Props {
    type: string
    userInfo: any
}
class UserForm extends React.Component<Props> {

    form = React.createRef<FormInstance>();

    getState = (state) => {
        return {
            '1': '',
            '2': '',
            '3': '',
            '4': 'FE',
            '5': ''
        }[state]
    }

    render() {
        let type = this.props.type;
        let userInfo = this.props.userInfo || {};
        const formItemLayout = {
            labelCol: { span: 5 },
            wrapperCol: { span: 19 }
        }
        return (
            <Form layout="horizontal" ref={this.form}>
                <FormItem label="" {...formItemLayout} name="username" initialValue={userInfo.username} rules={[
                    { message: '', required: type === 'detail' ? false : true }
                ]}>
                    {
                        type === 'detail' ? userInfo.username : (
                            <Input type="text" placeholder="" />
                        )
                    }
                </FormItem>
                <FormItem label="" {...formItemLayout} name="sex" initialValue={userInfo.sex} rules={[
                    { message: '', required: type === 'detail' ? false : true }
                ]}>
                    {
                        type === 'detail' ? userInfo.sex == 1 ? '' : '' : (
                            <RadioGroup>
                                <Radio value={1}></Radio>
                                <Radio value={2}></Radio>
                            </RadioGroup>
                        )
                    }
                </FormItem>
                <FormItem label="" {...formItemLayout} name="state" initialValue={userInfo.state} rules={[
                    { message: '', required: type === 'detail' ? false : true }
                ]}>
                    {
                        type === 'detail' ? this.getState(userInfo.state) : (
                            <Select placeholder="">
                                <Option value={1}></Option>
                                <Option value={2}></Option>
                                <Option value={3}></Option>
                                <Option value={4}>FE</Option>
                                <Option value={5}></Option>
                            </Select>
                        )
                    }
                </FormItem>
                <FormItem label="" {...formItemLayout} name="birthday" initialValue={moment(userInfo.birthday)} rules={[
                    { message: '', required: type === 'detail' ? false : true }
                ]}>
                    {
                        type === 'detail' ? userInfo.birthday : (
                            <DatePicker />
                        )
                    }
                </FormItem>
                <FormItem label="" {...formItemLayout} name="address" initialValue={userInfo.address} >
                    {
                        type === 'detail' ? userInfo.address : (
                            <TextArea autoSize={{ minRows: 3, maxRows: 5 }} placeholder="" />
                        )
                    }
                </FormItem>
            </Form>
        );
    }
}