import React from 'react'
import { Button, Card, DatePicker, Form, InputNumber, message, Radio, Select, Switch, Input, TimePicker, Checkbox, Upload, Modal } from 'antd'
import { UserOutlined, LockOutlined, UploadOutlined, PlusOutlined } from '@ant-design/icons';
import moment from 'moment';
import { common } from '../../common/public';

const FormItem = Form.Item;
const RadioGroup = Radio.Group;
const Option = Select.Option;
const { TextArea } = Input;
export default class FormReg extends React.Component {

    state: any = {
        previewVisible: false,
        previewImage: '',
        previewTitle: '',
        fileList: [
            {
                uid: '-1',
                name: 'image.png',
                status: 'done',
                url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
            },
            {
                uid: '-2',
                name: 'image.png',
                status: 'done',
                url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
            },
            {
                uid: '-3',
                name: 'image.png',
                status: 'done',
                url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
            },
            {
                uid: '-4',
                name: 'image.png',
                status: 'done',
                url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
            },
            {
                uid: '-xxx',
                percent: 50,
                name: 'image.png',
                status: 'uploading',
                url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
            },
            {
                uid: '-5',
                name: 'image.png',
                status: 'error',
            },
        ],
    };

    handleCancel = () => this.setState({ previewVisible: false });

    /**
     * 
     * @param file 
     */
    handlePreview = async file => {
        if (!file.url && !file.preview) {
            file.preview = await common.getBase64(file.originFileObj);
        }

        this.setState({
            previewImage: file.url || file.preview,
            previewVisible: true,
            previewTitle: file.name || file.url.substring(file.url.lastIndexOf('/') + 1),
        });
    };

    /**
     * 
     * @param param0 
     * @returns 
     */
    handleChange = ({ fileList }) => this.setState({ fileList });

    render() {
        const onFinish = (values: any) => {
            console.log(':', values);
            message.success(`${values.username} ${values.password}`)
        };

        const onFinishFailed = (errorInfo: any) => {
            console.log(':', errorInfo);
        };
        const formItemLayout = {
            labelCol: {
                xs: 24,
                sm: 4
            },
            wrapperCol: {
                xs: 24,
                sm: 12
            }
        }
        const offsetLayout = {
            wrapperCol: {
                xs: 24,
                sm: {
                    span: 12,
                    offset: 4
                }
            }
        }
        const rowObject = {
            minRows: 4, maxRows: 6
        }

        const normFile = (e: any) => {
            console.log(':', e);
            if (Array.isArray(e)) {
                return e;
            }
            return e && e.fileList;
        };


        const { previewVisible, previewImage, fileList, previewTitle } = this.state;
        const uploadButton = (
            <div>
                <PlusOutlined />
                <div style={{ marginTop: 8 }}></div>
            </div>
        );
        return (
            <div>
                <Card title="">
                    <Form
                        name="basic"
                        // labelCol={{ span: 8 }}
                        // wrapperCol={{ span: 16 }}
                        initialValues={{ remember: true }}
                        onFinish={onFinish}
                        onFinishFailed={onFinishFailed}
                        autoComplete="off">
                        <FormItem
                            label=""
                            name="username"
                            rules={[
                                {
                                    required: true,
                                    message: '!'
                                }
                            ]}
                            {...formItemLayout}
                        >
                            <Input prefix={<UserOutlined />} placeholder="" />
                        </FormItem>
                        <FormItem
                            label=""
                            name="password"
                            rules={[{ required: true, message: '!' }]}
                            {...formItemLayout}
                        >
                            <Input.Password prefix={<LockOutlined />} placeholder="" />
                        </FormItem>

                        <FormItem label="" name="sex" initialValue="1" required {...formItemLayout}>
                            <RadioGroup>
                                <Radio value="0"></Radio>
                                <Radio value="1"></Radio>
                            </RadioGroup>
                        </FormItem>

                        <FormItem label="" name="age" initialValue={18} rules={[
                            { required: true, message: '' }
                        ]} {...formItemLayout}>
                            <InputNumber max={100} />
                        </FormItem>


                        <FormItem label="" name="state" initialValue="2" required {...formItemLayout}>
                            <Select>
                                <Option value="1"></Option>
                                <Option value="2"></Option>
                                <Option value="3"></Option>
                                <Option value="4">FE</Option>
                                <Option value="5"></Option>
                            </Select>
                        </FormItem>

                        <FormItem label="" name="interest" initialValue={['2', '5']} required {...formItemLayout}>

                            <Select mode="multiple">
                                <Option value="1"></Option>
                                <Option value="2"></Option>
                                <Option value="3"></Option>
                                <Option value="4"></Option>
                                <Option value="5"></Option>
                                <Option value="6"></Option>
                                <Option value="7"></Option>
                                <Option value="8"></Option>
                            </Select>
                        </FormItem>

                        <FormItem label="" name="isMarried" initialValue={true} valuePropName="checked" required {...formItemLayout}>
                            <Switch style={{ width: 100 }} checkedChildren="" unCheckedChildren="" defaultChecked={true} />
                        </FormItem>

                        <FormItem label="" name="birthday" initialValue={moment('2021-09-16')} required {...formItemLayout} rules={[
                            { required: true, message: '' }
                        ]}
                        >
                            <DatePicker
                                showTime
                                format="YYYY-MM-DD HH:mm:ss"
                            />
                        </FormItem>

                        <FormItem label="" initialValue="" name="address" required {...formItemLayout} rules={[
                            { required: true, message: '' }
                        ]}>
                            <TextArea rows={4} autoSize={rowObject} />
                        </FormItem>


                        <FormItem label="" name="time" {...formItemLayout}>
                            <TimePicker />
                        </FormItem>

                        <Form.Item
                            name="userImg"
                            label=""
                            valuePropName="fileList"
                            getValueFromEvent={normFile}
                            extra=""
                            {...formItemLayout}
                        >
                            <Upload name="logo" action="/upload.do" listType="picture">
                                <Button icon={<UploadOutlined />}></Button>
                            </Upload>
                        </Form.Item>

                        <FormItem name="cardImg" label="" {...formItemLayout}>
                            <Upload
                                action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                                listType="picture-card"
                                fileList={fileList}
                                onPreview={this.handlePreview}
                                onChange={this.handleChange}
                            >
                                {fileList.length >= 8 ? null : uploadButton}
                            </Upload>


                        </FormItem>

                        <FormItem name="protocol" valuePropName="checked" {...offsetLayout} rules={[
                            {
                                validator: (_, value) =>
                                    value ? Promise.resolve() : Promise.reject(new Error('')),
                            },
                        ]}>
                            <Checkbox><a href="#"></a></Checkbox>
                        </FormItem>

                        <FormItem  {...offsetLayout}>
                            <Button type="primary" htmlType="submit">
                                
                            </Button>
                        </FormItem>
                    </Form>
                </Card>
                {/*  */}
                <Modal
                    visible={previewVisible}
                    title={previewTitle}
                    footer={null}
                    onCancel={this.handleCancel}
                >
                    <img alt="example" style={{ width: '100%' }} src={previewImage} />
                </Modal>
            </div>
        );
    }
}