import ArrowDownOutlined from '@ant-design/icons/lib/icons/ArrowDownOutlined';
import ArrowUpOutlined from '@ant-design/icons/lib/icons/ArrowUpOutlined';
import { Card, Col, message, Row, Spin, Statistic, Tabs } from 'antd';
import React from 'react';
import { echartsTheme } from '../../config/echartTheme';
import ReactECharts from 'echarts-for-react';
import './index.less'
const { TabPane } = Tabs;

export default class Home extends React.Component {

    getOption = () => {
        let option = {
            title: {
                text: '',
                x: 'left'
            },
            legend: {
                orient: 'vertical',
                right: 10,
                top: 20,
                bottom: 20,
                data: ['', '', '', '', '', '', '']
            },
            tooltip: {
                trigger: 'item',
                formatter: '{a}<br/>{b}:{c}({d}%)'
            },
            series: [
                {
                    name: '',
                    type: 'pie',
                    data: [
                        {
                            value: 1000,
                            name: ''
                        },
                        {
                            value: 1000,
                            name: ''
                        },
                        {
                            value: 2000,
                            name: ''
                        },
                        {
                            value: 1500,
                            name: ''
                        },
                        {
                            value: 3000,
                            name: ''
                        },
                        {
                            value: 2000,
                            name: ''
                        },
                        {
                            value: 1200,
                            name: ''
                        }
                    ]
                }
            ]
        }
        return option;
    }

    getOption2 = () => {
        let option = {
            title: {
                text: ''
            },
            tooltip: {
                trigger: 'axis'
            },
            xAxis: {
                data: ['', '', '', '', '', '', '']
            },
            yAxis: {
                type: 'value'
            },
            series: [
                {
                    name: '',
                    type: 'bar',
                    data: [1000, 2000, 1500, 3000, 2000, 1200, 800]
                }
            ]
        }
        return option;
    }

    getOption3 = () => {
        let option = {
            title: {
                text: ''
            },
            tooltip: {
                trigger: 'axis'
            },
            legend: {
                data: ['', '']
            },
            xAxis: {
                data: [
                    '', '', '', '', '', '', ''
                ]
            },
            yAxis: {
                type: 'value'
            },
            series: [
                {
                    name: '',
                    type: 'line',
                    data: [
                        1200,
                        3000,
                        4500,
                        6000,
                        8000,
                        12000,
                        20000
                    ]
                },
                {
                    name: '',
                    type: 'line',
                    data: [
                        1000,
                        2000,
                        5500,
                        6000,
                        8000,
                        10000,
                        12000
                    ]
                }
            ]
        }
        return option;
    }

    render() {
        return (
            <div>
                <Row gutter={16}>
                    <Col span={4}>
                        <Card>
                            <Statistic
                                title=""
                                value={33.15}
                                precision={2}
                                valueStyle={{ color: '#3f8600' }}
                                prefix={<ArrowUpOutlined />}
                                suffix="%"
                            />
                        </Card>
                    </Col>
                    <Col span={4}>
                        <Card>
                            <Statistic
                                title=""
                                value={30.3}
                                precision={2}
                                valueStyle={{ color: '#cf1322' }}
                                prefix={<ArrowDownOutlined />}
                                suffix="%"
                            />
                        </Card>
                    </Col>
                    <Col span={4}>
                        <Card>
                            <Statistic
                                title=""
                                value={25.28}
                                precision={2}
                                valueStyle={{ color: '#3f8600' }}
                                prefix={<ArrowUpOutlined />}
                                suffix="%"
                            />
                        </Card>
                    </Col>
                    <Col span={4}>
                        <Card>
                            <Statistic
                                title=""
                                value={45671}
                            // precision={2}
                            // valueStyle={{ color: '#cf1322' }}
                            // prefix={<ArrowDownOutlined />}
                            // suffix=""
                            />
                        </Card>
                    </Col>
                    <Col span={4}>
                        <Card>
                            <Statistic
                                title=""
                                value={1647891.15}
                                precision={2}
                            // valueStyle={{ color: '#3f8600' }}
                            // prefix={<ArrowUpOutlined />}
                            // suffix="%"
                            />
                        </Card>
                    </Col>
                    <Col span={4}>
                        <Card>
                            <Statistic
                                title=""
                                value={45678}
                            // precision={2}
                            // valueStyle={{ color: '#cf1322' }}
                            // prefix={<ArrowDownOutlined />}
                            // suffix="%"
                            />
                        </Card>
                    </Col>
                </Row>
                <Card style={{ marginTop: 20 }}>
                    <Tabs defaultActiveKey="1">
                        <TabPane tab="" key="1">
                            <Row style={{ marginTop: 20 }} gutter={16}>
                                <Col span={12}><ReactECharts option={this.getOption()} theme={echartsTheme.theme} style={{ height: 450 }} /></Col>
                                <Col span={12}><ReactECharts option={this.getOption2()} theme={echartsTheme.theme} style={{ height: 450 }} /></Col>
                            </Row>
                        </TabPane>
                        <TabPane tab="" key="2">
                            <ReactECharts option={this.getOption3()} theme={echartsTheme.theme} style={{ height: 450, marginTop: 20 }} />
                        </TabPane>
                    </Tabs>
                </Card>
            </div>
        );
    }
}