import React from 'react';
/*  */
import { ConfigProvider } from 'antd';
import 'moment/locale/zh-cn';
import locale from 'antd/lib/locale/zh_CN';

export default class App extends React.Component {
  render() {
    return (
      /*  */
      <div>
        {/*  ConfigProvider */}
        <ConfigProvider locale={locale}>
          {this.props.children}
        </ConfigProvider>
      </div>
    );
  }
}