import React from 'react';
import { Button, Card, message, Modal, Table } from 'antd';
import { tableServices } from './service';
import { common } from '../../common/public';


export default class BasicTable extends React.Component {

    state = {
        dataSource: [],
        mockData: [],
        // 
        selectedRowKeys: [],
        // 
        selectedRowKeyCheck: [],
        selectedItem: [],
        loading: false,
        // 
        pageData: [],
        curPage: 1,
        pageSize: 10,
        total: 30,
        pagination: null,
    }


    componentDidMount() {
        const data = [
            {
                id: '0',
                userName: 'Jack',
                sex: '1',
                state: '1',
                interest: '1',
                birthday: '2000-01-01',
                address: '',
                time: '09:00'
            },
            {
                id: '1',
                userName: 'Tom',
                sex: '1',
                state: '3',
                interest: '1',
                birthday: '2000-01-01',
                address: '',
                time: '09:00'
            },
            {
                id: '2',
                userName: 'Lily',
                sex: '2',
                state: '2',
                interest: '1',
                birthday: '2000-01-01',
                address: '',
                time: '09:00'
            },
        ]
        data.map((item: any, index) => {
            item.key = index;
        })
        this.setState({
            dataSource: data
        })
        this.getTableList();
        this.getTablePageList();
    }

    /**
     * mock
     */
    getTableList = () => {
        tableServices.getTableList().then(res => {
            // console.log(res);
            if (res.status === 0) {
                let data = res.list;
                data.map((item: any, index) => {
                    item.key = index;
                })
                this.setState({
                    mockData: data,
                })
            } else {
                message.info(res.msg);
            }
        })
    }

    /**
     * mock
     */
    getTablePageList = () => {
        const params = {
            page: this.state.curPage
        }
        tableServices.getTablePageList(params).then(res => {
            if (res.status === 0) {
                let data = res.list;
                data.map((item: any, index) => {
                    item.key = index;
                })
                this.setState({
                    pageData: data,
                    curPage: res.page,
                    pageSize: res.pageSize,
                    total: res.total,
                    pagination: common.pagination(res, (current) => {
                        /* TODO (mock) */
                        this.state.curPage = current;
                        this.getTablePageList();
                    })
                })
            }
        })
    }

    // 
    onRowClick = (record, list) => {
        let selectKey = [list];
        message.info(`${record.userName},${record.interest}`)

        this.setState({
            selectedRowKeys: selectKey,
        })
    }

    // 
    handleDelete = () => {
        this.setState({ loading: true });
        const { selectedRowKeyCheck, selectedItem } = this.state;
        Modal.confirm({
            title: '',
            content: `${selectedRowKeyCheck.join(',')}`,
            onOk: () => {
                message.success('');
                setTimeout(() => {
                    this.setState({
                        selectedRowKeyCheck: [],
                        loading: false,
                    });
                    console.log('', selectedItem);
                }, 500);
            }
        })

    }

    // 
    onSelectChange = (selectedRowKeyCheck, item) => {
        console.log('', selectedRowKeyCheck, item);
        this.setState({ selectedRowKeyCheck, selectedItem: item });
    };

    render() {
        const { dataSource, mockData, selectedRowKeys, selectedRowKeyCheck, loading, pageData, pagination } = this.state
        const columns = [
            {
                title: 'id',
                key: 'id',
                dataIndex: 'id'
            },
            {
                title: '',
                key: 'userName',
                dataIndex: 'userName'
            },
            {
                title: '',
                key: 'sex',
                dataIndex: 'sex',
                render: (sex: number) => {
                    return sex == 1 ? '' : ''
                }
            },
            {
                title: '',
                key: 'state',
                dataIndex: 'state',
                render: (state: number) => {
                    let config = {
                        '1': '',
                        '2': '',
                        '3': '',
                        '4': 'FE',
                        '5': ''
                    }
                    return config[state];
                }
            },
            {
                title: '',
                key: 'interest',
                dataIndex: 'interest',
                render: (abc: number) => {
                    let config = {
                        '1': '',
                        '2': '',
                        '3': '',
                        '4': '',
                        '5': '',
                        '6': '',
                        '7': '',
                        '8': ''
                    }
                    return config[abc];
                }
            },
            {
                title: '',
                key: 'birthday',
                dataIndex: 'birthday'
            },
            {
                title: '',
                key: 'address',
                dataIndex: 'address'
            },
            {
                title: '',
                key: 'time',
                dataIndex: 'time'
            }
        ]
        // 
        const rowSelection: any = {
            type: 'radio',
            selectedRowKeys,
        };
        // 
        const rowSelections = {
            selectedRowKeys: selectedRowKeyCheck,
            onChange: this.onSelectChange,
        };
        const hasSelected = selectedRowKeyCheck.length > 0;
        return (
            <div>
                <Card title="" className="card-wrap">
                    <Table bordered dataSource={dataSource} columns={columns} pagination={false} />
                </Card>
                <Card title="-Mock" className="card-wrap">
                    <Table bordered dataSource={mockData} columns={columns} pagination={false} />
                </Card>
                <Card title="Mock-" className="card-wrap">
                    <Table rowSelection={rowSelection} bordered dataSource={mockData} columns={columns} pagination={false} onRow={(record, list) => {
                        return {
                            onClick: () => { this.onRowClick(record, list) }
                        }
                    }} />
                </Card>
                <Card title="Mock-" className="card-wrap">
                    <div style={{ marginBottom: 16 }}>
                        <Button type="primary" onClick={this.handleDelete} disabled={!hasSelected} loading={loading}>
                            
                        </Button>
                        <span style={{ marginLeft: 8 }}>
                            {hasSelected ? ` ${selectedRowKeyCheck.length} ` : ''}
                        </span>
                    </div>
                    <Table rowSelection={rowSelections} bordered dataSource={mockData} columns={columns} pagination={false} />
                </Card>
                <Card title="Mock-" className="card-wrap">
                    <Table bordered dataSource={pageData} columns={columns} pagination={pagination} />
                </Card>
            </div>
        );
    }
}